.gpt3__blog-container_article{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}
.gpt3__blog-container_article-image{
    width: 100%;
    height: 100%;
    background: var(--color-footer);
}
.gpt3__blog-container_article-image img{
    width: 100%;
    height: 100%;
}
.gpt__blog-container_article-content{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}
.gpt__blog-container_article-content p{
 

font-family: var(--font-family);
font-style: normal;
font-weight: 700;
font-size: 11.649px;
line-height: 35px;
/* identical to box height, or 300% */

color: #FFFFFF;


}
.gpt__blog-container_article-content h3{
    /* GPT-3 and Open AI is the future. Let us exlore how it is? */


font-family: var(--font-family);
font-style: normal;
font-weight: 800;
font-size: 25.11px;
line-height: 30px;
/* or 121% */

color: #FFFFFF;
margin-bottom: 5rem;


}
.gpt__blog-container_article-content p:last-child{
    cursor: pointer;
}
@media screen and (max-width:550px) {
    .gpt__blog-container_article-content h3{
        font-size: 18px;
    }
}