.gpt3__blog{
    display: flex;
    flex-direction: column;
}
.gpt3__blog-heading{

 width: 100%;
 text-align: left;
 margin-bottom: 5rem;

font-family: var(--font-family);

font-weight: 800;
font-size: 28px;
line-height: 70px;
/* or 121% */
letter-spacing: -0.04em;



}
.gpt3__blog-container{
    display: flex;
    flex-direction: row;
}
.gpt3__blog-container_groupA{
flex: 0.75;
margin-right: 2rem;
}
.gpt3__blog-container_groupB{
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 2rem;
}
@media screen and (max-width:990px) {
    .gpt3__blog-container{
        flex-direction: column-reverse;
    }
    .gpt3__blog-container_groupB{
        margin: 2rem 0;
    }
.gpt3__blog-container_groupA .gpt3__blog-container_article{
    width: 49%;
}
.gpt3__blog-container_groupA .gpt3__blog-container_article-image{
    height: 250px;
}
}
@media screen and (max-width:700px) {
    .gpt3__blog-container_groupB{
        grid-template-columns: repeat(1,1fr);
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article{
        width: 110%;
    }
    .gpt3__blog-heading{
        margin-bottom: 1rem;
    }
    .gpt3__blog-heading h1{
        font-size: 26px;
padding-bottom: 1rem;
        line-height: 22px;
    }
    
}